/**
 * mall
 * 
 */
export const integraList = (num) => fetch(mapiUrl, {
    act:'uc_goods_order',
    email:user_name,
    pwd:user_pwd,
    page:num,
    mode:1,
    start_time: '0',
    end_time: '0',
}, 'GET')
/**
 * mall
 * 
 */
export const preferred = (num) => fetch(mapiUrl, {
    act: 'goodslists',
    cate_id: null,
    is_recommend: 1,
    page: null,
    score: null,
    market_price: null,
    add_time: null
}, 'GET')
/**
 * mall
 * 
 */
export const commodityList = (cateId,mark,pageCode,scoreCode,priceCode,timeCode) => fetch(mapiUrl, {
    act: 'goodslists',
    cate_id: cateId,   //
    is_recommend: mark, //1
    page: pageCode,
    score: scoreCode,        //
    market_price: priceCode,  //
    add_time: timeCode      //
}, 'GET')
